.step-selector{
  @include title-box;
  @include round-box;
  @include modal-box;
  display: flex;
  
  .tree, .data{
    overflow: hidden scroll;
    max-height: 70vh;
    word-wrap: word-break;
    box-sizing: border-box;
  }

  
  .step-selector-confirm{ 
    .w3-modal-content{
      @include round-box;
      padding: 1rem;
    }
    input[type='text']{
      width: 100%;
    }
  }
    
  .tree{
    flex: 1;
    background-color: $color-layer-1;
    
    .selected>h3{
    background-color: $color-layer-2;
    }

    .tree-node{
      cursor: pointer;
      
      .children{
        padding-left: 1rem;
      }
      h3{
        font-size: 1rem;
      }
    }
  }

  .data{
    @include table();
    flex: 3;
    background-color: $color-layer-0;
  }
}