.step{
  @include round-box();
  margin: .5rem 0;
  background-color: var(--color-layer-1, whitesmoke);
  padding: 1.5rem 2rem;
  overflow: hidden;
  
  span:nth-child(3){
    word-break: break-all;
  }
  
  span:last-of-type{
    text-align: end;
    word-wrap: none!important;
    display: inline-block;
  }
  
  input {
    background-color: white;
    border: none;
    padding: .2rem .5rem;
    margin: 0 1px;
    box-sizing: border-box;
  }

  input[type=button]{
    @include clickable();
  }
  
  input[type=text]{
    width: max-content;
  }
}