$color-layer-0: white;
$color-layer-1: whitesmoke;
$color-layer-2: lightgray;
$color-layer-3: gray;
$top-bar-height: 3rem;


@mixin round-box {
  border-radius: 1rem;
}

@mixin title-box {
  background-color: var(--color-layer-2, lightgray);
  padding-top: 3rem;
  >h1, >h2, >h3{
    font-size: 1.5rem;
  }
  .close{
    font-size: 1.5rem;
  }
}

@mixin modal-box {
  box-shadow: var(--color-layer-3, gray) 0 0 1rem;
  width: 90vw;
  overflow: hidden scroll;
  max-height: 70vh;
}

@mixin clickable($baseBgColor: inherit) {
  transition: all .2s ease-in-out;
  background-color: $baseBgColor;
  cursor: pointer;
  &:hover{
    background-color: $color-layer-2;
  }
  &:active{
    background-color: $color-layer-0;
  }
}

@mixin button($baseBgColor: inherit) {
  @include round-box;
  @include clickable($baseBgColor);
  border-bottom: 1px solid #ccc;
  margin: 10px 0;
  height: 3rem;
  padding: 0.75rem!important;
  text-align: center;
  &:active{
    border-bottom: 0px;
    border-top: 1px solid #ccc;
  }
}

@mixin round-button{ 
  @include round-box;
  @include clickable(); 
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
}

@mixin round-button-dark {
  @include round-button();
  &:hover{
    background-color: $color-layer-3;
  }
  &:active{
    background-color: $color-layer-1;
  }
}

@mixin sidebar-left( $width: 33vw, $widthMobile: 66vw){
  background-color: var(--color-layer-1, whitesmoke);
  overflow-y: scroll;
  height:calc( 100vh - #{$top-bar-height});
  z-index: 1;
  transition: 0.2s linear;
  box-shadow: 0 2px 2px 2px lightgray;
  width: $width;
  @media (max-width:601px) {
    position: fixed;
    width: $widthMobile;
    box-sizing: border-box;
    
    &.hidden{
      margin-left: calc(100% * -1);
    }
  }
}

@mixin table {
  table{
    tr{
      @include clickable();
      
      th{
        word-wrap: none;
        min-width: 70px;  
      }
    }
  }
}