.schedule{
    max-height: calc( 100vh - #{$top-bar-height});
    overflow: hidden scroll;        
    
    .conversion-selector select, .button {
        @include button($color-layer-1);
    }
    
    .steps{
        max-height: 80vh;
        overflow-y: scroll;
        width: 100%;
        display: block;
        padding-bottom: 10vh;
    }

    .footer{
        position: sticky;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        background-color: $color-layer-0;
        
        .add-step{
            width: 200px;
        }
        
        .sum{
            padding: 1rem;
            border-top: 1px solid var(--color-layer-3, gray);
        }
    }
}